/**
 * geturl
 * @param {object}} data 
 */
let paramsToQueryString = (data) => {
    let queryString = '?';
    data.token = getToken();
    Object.keys(data).forEach(key => {
        const value = data[key];
        queryString += `${key}=${value}&`
    })
    return queryString;
}


