import axios from 'axios'
import {
    Message, Loading
} from 'element-ui';
axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8'

import router from '../router/index'

import {
    getToken,
    setToken,
    setLocalStorageToken,
    removeTokenAll
} from '@/utils/auth'
import {
    debounce
} from '@/utils/index'


import { baseUrl } from '../api/baseUrl'; //
let loadingInstance;//

//
const notice = (msg, type = "warning", duration = 2000) => {
    if (notice.timer) clearTimeout(notice.timer);
    notice.timer = setTimeout(() => {
        clearTimeout(notice.timer);
        notice.timer = null;
        notice.count = 0;
        Message({
            message: msg || '',
            type,
            duration,
        });
    }, notice.count >= 2 ? 1000 : 0);//2
}

notice.count = 0;//0

const showLoading = function () {
    loadingInstance = Loading.service({ fullscreen: true, background: 'rgba(255,255,255,0)' });
}


const request = axios.create({
    // axiosbaseURLURL
    baseURL: `${baseUrl}/dldf/`,
    // 
    timeout: 10000
})

request.interceptors.request.use((config) => {
    notice.count += 1;//
    //token
    if (getToken()) {
        config.headers['uapAccessToken'] = getToken('accessToken');
        config.headers['uapRefreshToken'] = getToken('refreshToken');
    }

    config.headers['X-Requested-With'] = "XMLHttpRequest";

    if (config.params && config.params._loading !== undefined && config.params._loading == false || (config.data && config.data._loading !== undefined && config.data._loading == false)) { } else {
        showLoading(config);
    }
    return config;
})

request.interceptors.response.use((response) => {
    //refreshTokenaccessToken
    if (response.headers && response.headers.uapaccesstoken) {
        setToken('accessToken', response.headers.uapaccesstoken);
        setLocalStorageToken('accessToken', response.headers.uapaccesstoken);
    }
    loadingInstance && loadingInstance.close();//
    let { code, msg, message } = response.data;
    if (code && code != 200) {//codearrybuffer
        notice(msg ? msg : message);
        return Promise.reject(response.data);
    }
    return response.data
}, err => {
    // 
    if (err && err.stack.indexOf('timeout') > -1) {
        notice('');
    } else if (err.stack.indexOf('401') > -1) {
        notice('');
        removeTokenAll();// token
        setTimeout(() => {
            router.push("/login")
        }, 1000);
    }
    loadingInstance && loadingInstance.close();//
    return Promise.reject(err);
})



const get = function (url, params, responseType) {
    const opt = {
        method: 'get',
        url,
        params,
    }
    if (responseType) {
        opt.responseType = responseType;
    }
    return request(opt)
}

const post = function (url, data) {
    return request({
        method: 'post',
        url,
        data
    })
}
const put = function (url, params) {
    return request({
        method: 'put',
        url,
        params,
    })
}

export {
    request,
    get,
    post,
    put
};